export function rhythmTitle(value) {
  let result;
  // console.log(value);
  if (value < 55) {
    result = 'Bad';
  } else if (value > 59) {
    result = 'Medium';
  } else if (value > 60 || value < 80) {
    result = 'Good';
  } else if (value > 80 || value < 85) {
    result = 'Medium';
  } else if (value > 86) {
    result = 'Bad';
  } else {
    result = 'Try again';
  }
  return result;
}
